<script>
export default {
  props: {
    id: { type: String },
  },
  data() {
    return {
      //  
      status: [
        { value: 1, label: "" },
        { value: 2, label: "" },
        { value: 3, label: "" },
      ],
      // 
      meters: [],
      // 
      supervisors: [],
      // 
      containers: [],
      // 
      mediums: [],
      model: {
        company: "",
        code: null,
        address: "",
        route: "",
        contacts: [],

        // 
        meter: null,

        container: null,
        otherCondition: "",
        meterType: "",
        useTime: "",
        medium: "",
        liquidMedium: "",
        viscosity: "",
        desity: "",
        temperatureRange: "",
        pressureRange: "",
        liquidLevelRange: "",
        userPreferences: [],
        // 
        maxVoltage: {
          voltage: "",
          calibratingSignal: "",
        },
        minVoltage: {
          voltage: "",
          calibratingSignal: "",
        },
        electricity: "",
        liquidLevel: "",
        resistance: "",
        intrinsicPreferences: [],
        // intrinsicParameter: "",
        installationDiagram: "",
        // 
        time: "",
        contrastLiquidLevel: "",
        meterliquidLevel: "",
        meterSignal: "",
        signalFileName: "",
        mediumPressure: "",
        mediumTemperature: "",
        status: {
          value: null,
          label: "",
        },
        signalFigure: "",
        // 
        meterPanorama: "",
        meterClose: "",
        pipelinePanorama: "",
        probePanorama: "",
        // 
        supervisor: null,
        // 
        otherContent: "",
        // 
        reportStatus: "",
        // 
        submitter: "",
      },
    };
  },
  methods: {
    // 
    async save() {
      // this.$http.post()
      let res;
      if (this.id) {
        this.model.submitter = localStorage.id;
        this.model.reportStatus = "";
        // console.log(this.model);
        // 
        res = await this.$http.put(
          `rest/installationReports/${this.id}`,
          this.model
        );
      } else {
        // 
        this.model.submitter = localStorage.id;
        this.model.reportStatus = "";
        res = await this.$http.post(`rest/installationReports/`, this.model);
      }
      this.$router.push("/installationReports/list");
      this.$message({
        type: "success",
        message: "",
      });
    },
    // 
    async fetch() {
      const res = await this.$http.get(`rest/installationReports/${this.id}`);
      this.model = Object.assign({}, this.model, res.data.pop());
    },
    // 
    afterUpload1(res) {
      // this.$set(this.model,'avatar',res.url)
      this.model.installationDiagram = res.url;
    },
    // 
    afterUpload2(res) {
      // this.$set(this.model,'avatar',res.url)
      this.model.signalFigure = res.url;
    },
    // 
    afterUpload3(res) {
      // this.$set(this.model,'avatar',res.url)
      this.model.meterPanorama = res.url;
    },
    // 
    afterUpload4(res) {
      // this.$set(this.model,'avatar',res.url)
      this.model.meterClose = res.url;
    },
    // 
    afterUpload5(res) {
      // this.$set(this.model,'avatar',res.url)
      this.model.pipelinePanorama = res.url;
    },
    // 
    afterUpload6(res) {
      // this.$set(this.model,'avatar',res.url)
      this.model.probePanorama = res.url;
    },
    // 
    async fetchMeters() {
      const res = await this.$http.get(`rest/meters`);
      this.meters = res.data;
      // console.log(this.meters);
    },

    // 
    async fetchSupervisor() {
      const res = await this.$http.get(`rest/accounts`);
      this.supervisors = res.data;
      // console.log(this.supervisors);
    },
    // 
    async fetchContainer() {
      const res = await this.$http.get(`rest/containers`);
      this.containers = res.data;
    },
    // 
    async fetchMedium() {
      const res = await this.$http.get(`rest/mediums`);
      this.mediums = res.data;
    },
    select(item) {
      this.model.status.label = item.label;
      this.model.status.value = item.value;
    },
  },
  created() {
    this.fetchMeters();
    this.fetchContainer();
    this.fetchMedium();
    this.fetchSupervisor();
    this.id && this.fetch();
  },
};
</script>

<style scoped>
.title {
  color: #606266;
  border-left: 6px solid #409eff;
  padding-left: 2rem;
  font-size: 1em;
}
</style>
